<script>
import { Table, TableColumn, Select, Option } from "element-ui";
import moment from "moment";
import perusahaan from "../perusahaan";
import cabang from "../cabang";
import pemenuhan from "./pemenuhan";
import rating from "./rating";
import kp from "./kp";
import pg from "./pg";
import pl from "./pl";
import mk from "./mk";
import ku from "./ku";
import kl from "./kl";
import at from "./at";
import dp from "./dp";
import dk from "./dk";
import em from "./em";
import ai from "./ai";
import hs from "./hs";
import ml from "./ml";
import lb from "./lb";
import sb from "./sb";
import flatPicker from "vue-flatpickr-component";
import "flatpickr/dist/flatpickr.css";
import swal from "sweetalert2";
import "sweetalert2/dist/sweetalert2.css";

const axios = require("axios");
const rateRNG = () => {
  let num = Math.random();
  if (num > 0.75) return 2;
  else if (num > 0.5) return 1;
  else if (num > 0.25) return 0;
  else return -2;
};
const intRNG = (min, max) => {
  return Math.floor(Math.random() * (max - min) + min);
};
const tsRNG = () => {
  return intRNG(1262304000000, 1577836800000);
};

export default {
  components: {
    flatPicker,
    [Select.name]: Select,
    [Option.name]: Option,
    [Table.name]: Table,
    [TableColumn.name]: TableColumn,
  },
  data() {
    return {
      api: this.$store.state.api,
      form: "kp",
      perusahaan,
      cabang,
      pemenuhan,
      periode: [],
      dates: {
        ratingDate: new Date(),
        config: {
          altFormat: "d/m/Y",
          altInput: true,
          allowInput: true,
          clickOpens: true,
          dateFormat: "U",
        },
      },
      selects: {
        companyId: "PT Pelindo 2",
        unitId: null,
        periode: null,
      },
      ratingData: rating,
      tableData: {
        kp: kp,
        pg: pg,
        pl: pl,
        mk: mk,
        ku: ku,
        kl: kl,
        at: at,
        dp: dp,
        dk: dk,
        em: em,
        ai: ai,
        hs: hs,
        ml: ml,
        lb: lb,
        sb: sb,
      },
    };
  },
  computed: {
    isAdmin() {
      return this.$store.getters.isAdmin;
    },
    submitData() {
      let data = {
        c_id: this.selects.companyId,
        uk_id: this.selects.unitId,
        timestamp: this.dates.ratingDate * 1000,
        periode: this.selects.periode,
        kp: [],
        pg: [],
        pl: [],
        mk: [],
        ku: [],
        kl: [],
        at: [],
        dp: [],
        dk: [],
        em: [],
        ai: [],
        hs: [],
        ml: [],
        lb: [],
        sb: [],
      };
      this.tableData.kp.forEach((element) => {
        data.kp.push(element.value);
      });
      this.tableData.pg.forEach((element) => {
        data.pg.push(element.value);
      });
      this.tableData.pl.forEach((element) => {
        data.pl.push(element.value);
      });
      this.tableData.mk.forEach((element) => {
        data.mk.push(element.value);
      });
      this.tableData.ku.forEach((element) => {
        data.ku.push(element.value);
      });
      this.tableData.kl.forEach((element) => {
        data.kl.push(element.value);
      });
      this.tableData.at.forEach((element) => {
        data.at.push(element.value);
      });
      this.tableData.dp.forEach((element) => {
        data.dp.push(element.value);
      });
      this.tableData.dk.forEach((element) => {
        data.dk.push(element.value);
      });
      this.tableData.em.forEach((element) => {
        data.em.push(element.value);
      });
      this.tableData.ai.forEach((element) => {
        data.ai.push(element.value);
      });
      this.tableData.hs.forEach((element) => {
        data.hs.push(element.value);
      });
      this.tableData.ml.forEach((element) => {
        data.ml.push(element.value);
      });
      this.tableData.sb.forEach((element) => {
        data.sb.push(element.value);
      });
      this.tableData.lb.forEach((element) => {
        data.lb.push(element.value);
      });
      return data;
    },
  },
  methods: {
    getListPeriode() {
      axios
        .get("http://45.127.133.75:3000/api/iems/list-periode")
        .then((res) => {
          this.periode = res.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    randomizeData() {
      Object.keys(this.tableData).forEach((key) => {
        for (let index = 0; index < this.tableData[key].length; index++) {
          this.tableData[key][index].value = rateRNG();
        }
      });
      this.dates.ratingDate = tsRNG();
    },
    passData(data) {
      this.selects.companyName = data.companyName;
      this.selects.periode = data.periode;
      this.selects.unitId = this.getUnitId(data.unitName);
      this.dates.ratingDate = data.timestamp * 1000;
      Object.keys(this.tableData).forEach((key) => {
        for (let index = 0; index < this.tableData[key].length; index++) {
          this.tableData[key][index].value = data[key][index];
          this.pemenuhanChange(this.tableData[key][index].code);
        }
      });
    },
    getUnitName(id) {
      for (let index = 0; index < this.cabang.length; index++) {
        if (this.cabang[index].value === id) {
          return this.cabang[index].label;
        }
      }
    },
    getUnitId(name) {
      for (let index = 0; index < this.cabang.length; index++) {
        if (this.cabang[index].label === name) {
          return this.cabang[index].value;
        }
      }
    },
    submitHandler() {
      swal
        .fire({
          title: `Konfirmasi`,
          text: "Apakah anda yakin untuk menyimpan data?",
          showCancelButton: true,
          icon: "question",
        })
        .then((result) => {
          if (result.isConfirmed) {
            axios
              .put(
                this.api.url +
                  this.api.gp.putRating +
                  this.$store.getters.getGPData.id,
                this.submitData
              )
              .then((response) => {
                swal.fire("Data tersimpan", "", "success").then(() => {
                  this.$router.push("DaftarRekapitulasi");
                });
              })
              .catch((error) => {
                swal.fire("Data tidak tersimpan", "ERROR: " + error, "error");
              });
          } else {
            swal.fire("Data tidak tersimpan", "", "error");
          }
        });
    },
    formSelect(code) {
      this.form = code;
    },
    pemenuhanHandler(event, index, row) {
      switch (event) {
        case -2:
          row.status = "N/A";
          break;
        case 1:
          row.status = "Kurang";
          break;
        case 2:
          row.status = "Ya";
          break;

        default:
          row.status = "Tidak";
          break;
      }
      this.pemenuhanChange(row.code);
    },
    pemenuhanChange(code) {
      switch (code) {
        case "KP01":
          this.percentageHandler("kp", 0, 5);
          break;
        case "KP02":
          this.percentageHandler("kp", 5, 3);
          break;
        case "KP03":
          this.percentageHandler("kp", 8, 4);
          break;
        case "KP04":
          this.percentageHandler("kp", 12, 3);
          break;
        case "KP05":
          this.percentageHandler("kp", 15, 5);
          break;
        case "KP06":
          this.percentageHandler("kp", 20, 3);
          break;

        case "PG01":
          this.percentageHandler("pg", 0, 5);
          break;
        case "PG02":
          this.percentageHandler("pg", 5, 5);
          break;
        case "PG03":
          this.percentageHandler("pg", 10, 6);
          break;

        case "PL01":
          this.percentageHandler("pl", 0, 1);
          break;
        case "PL02":
          this.percentageHandler("pl", 1, 1);
          break;
        case "PL03":
          this.percentageHandler("pl", 2, 3);
          break;
        case "PL04":
          this.percentageHandler("pl", 5, 1);
          break;
        case "PL05":
          this.percentageHandler("pl", 6, 1);
          break;
        case "PL06":
          this.percentageHandler("pl", 7, 2);
          break;
        case "PL07":
          this.percentageHandler("pl", 9, 5);
          break;

        case "MK01":
          this.percentageHandler("mk", 0, 3);
          break;
        case "MK02":
          this.percentageHandler("mk", 3, 5);
          break;
        case "MK03":
          this.percentageHandler("mk", 8, 3);
          break;

        case "KU01":
          this.percentageHandler("ku", 0, 4);
          break;
        case "KU02":
          this.percentageHandler("ku", 4, 3);
          break;
        case "KU03":
          this.percentageHandler("ku", 7, 2);
          break;
        case "KU04":
          this.percentageHandler("ku", 9, 3);
          break;
        case "KU05":
          this.percentageHandler("ku", 12, 4);
          break;
        case "KU06":
          this.percentageHandler("ku", 16, 3);
          break;
        case "KU07":
          this.percentageHandler("ku", 19, 1);
          break;
        case "KU08":
          this.percentageHandler("ku", 20, 6);
          break;

        case "KL01":
          this.percentageHandler("kl", 0, 2);
          break;
        case "KL02":
          this.percentageHandler("kl", 2, 3);
          break;
        case "KL03":
          this.percentageHandler("kl", 5, 2);
          break;
        case "KL04":
          this.percentageHandler("kl", 7, 4);
          break;

        case "AT01":
          this.percentageHandler("at", 0, 1);
          break;
        case "AT02":
          this.percentageHandler("at", 1, 1);
          break;
        case "AT03":
          this.percentageHandler("at", 2, 1);
          break;
        case "AT04":
          this.percentageHandler("at", 3, 1);
          break;
        case "AT05":
          this.percentageHandler("at", 4, 1);
          break;
        case "AT06":
          this.percentageHandler("at", 5, 1);
          break;

        case "DP01":
          this.percentageHandler("dp", 0, 1);
          break;
        case "DP02":
          this.percentageHandler("dp", 1, 1);
          break;
        case "DP03":
          this.percentageHandler("dp", 2, 1);
          break;
        case "DP04":
          this.percentageHandler("dp", 3, 1);
          break;
        case "DP05":
          this.percentageHandler("dp", 4, 1);
          break;
        case "DP06":
          this.percentageHandler("dp", 5, 1);
          break;
        case "DP07":
          this.percentageHandler("dp", 6, 1);
          break;
        case "DP08":
          this.percentageHandler("dp", 7, 10);
          break;

        case "DK01":
          this.percentageHandler("dk", 0, 2);
          break;
        case "DK02":
          this.percentageHandler("dk", 2, 2);
          break;

        case "EM01":
          this.percentageHandler("em", 0, 6);
          break;
        case "EM02":
          this.percentageHandler("em", 6, 8);
          break;
        case "EM03":
          this.percentageHandler("em", 14, 3);
          break;

        case "AI01":
          this.percentageHandler("ai", 0, 3);
          break;
        case "AI02":
          this.percentageHandler("ai", 3, 2);
          break;
        case "AI03":
          this.percentageHandler("ai", 5, 5);
          break;
        case "AI04":
          this.percentageHandler("ai", 10, 2);
          break;

        case "HS01":
          this.percentageHandler("hs", 0, 1);
          break;
        case "HS02":
          this.percentageHandler("hs", 1, 2);
          break;
        case "HS03":
          this.percentageHandler("hs", 3, 2);
          break;
        case "HS04":
          this.percentageHandler("hs", 5, 2);
          break;
        case "HS05":
          this.percentageHandler("hs", 7, 1);
          break;

        case "ML01":
          this.percentageHandler("ml", 0, 3);
          break;
        case "ML02":
          this.percentageHandler("ml", 3, 1);
          break;
        case "ML03":
          this.percentageHandler("ml", 4, 1);
          break;
        case "ML04":
          this.percentageHandler("ml", 5, 1);
          break;
        case "ML05":
          this.percentageHandler("ml", 6, 11);
          break;

        case "LB01":
          this.percentageHandler("lb", 0, 11);
          break;
        case "LB02":
          this.percentageHandler("lb", 11, 2);
          break;
        case "LB03":
          this.percentageHandler("lb", 13, 3);
          break;
        case "LB04":
          this.percentageHandler("lb", 16, 4);
          break;
        case "LB05":
          this.percentageHandler("lb", 20, 1);
          break;
        case "LB06":
          this.percentageHandler("lb", 21, 2);
          break;
        case "LB07":
          this.percentageHandler("lb", 23, 2);
          break;
        case "LB08":
          this.percentageHandler("lb", 25, 2);
          break;
        case "LB09":
          this.percentageHandler("lb", 27, 5);
          break;

        case "SB01":
          this.percentageHandler("sb", 0, 2);
          break;
        case "SB02":
          this.percentageHandler("sb", 2, 2);
          break;
        case "SB03":
          this.percentageHandler("sb", 4, 2);
          break;

        default:
          break;
      }
    },
    percentageHandler(object, start, span) {
      let sum = 0;
      for (let i = start; i < start + span; i++) {
        sum += Math.abs(this.tableData[object][i].value);
      }
      let percentage =
        (sum / (span * 2)) * this.tableData[object][start].portion * 100;
      for (let j = start; j < start + span; j++) {
        this.tableData[object][j].percentage = percentage;
      }
    },
    arraySpanMethodKP({ row, column, rowIndex, columnIndex }) {
      if (
        columnIndex === 0 ||
        columnIndex === 1 ||
        columnIndex === 2 ||
        columnIndex === 7
      ) {
        if (rowIndex === 0) return [5, 1];
        else if (rowIndex === 5) return [3, 1];
        else if (rowIndex === 8) return [4, 1];
        else if (rowIndex === 12) return [3, 1];
        else if (rowIndex === 15) return [5, 1];
        else if (rowIndex === 20) return [3, 1];
        else return [0, 0];
      }
    },
    arraySpanMethodPG({ row, column, rowIndex, columnIndex }) {
      if (
        columnIndex === 0 ||
        columnIndex === 1 ||
        columnIndex === 2 ||
        columnIndex === 7
      ) {
        if (rowIndex === 0) return [5, 1];
        else if (rowIndex === 5) return [5, 1];
        else if (rowIndex === 10) return [6, 1];
        else return [0, 0];
      }
    },
    arraySpanMethodPL({ row, column, rowIndex, columnIndex }) {
      if (
        columnIndex === 0 ||
        columnIndex === 1 ||
        columnIndex === 2 ||
        columnIndex === 7
      ) {
        if (rowIndex === 0) return [1, 1];
        else if (rowIndex === 1) return [1, 1];
        else if (rowIndex === 2) return [3, 1];
        else if (rowIndex === 5) return [1, 1];
        else if (rowIndex === 6) return [1, 1];
        else if (rowIndex === 7) return [2, 1];
        else if (rowIndex === 9) return [5, 1];
        else return [0, 0];
      }
    },
    arraySpanMethodMK({ row, column, rowIndex, columnIndex }) {
      if (
        columnIndex === 0 ||
        columnIndex === 1 ||
        columnIndex === 2 ||
        columnIndex === 7
      ) {
        if (rowIndex === 0) return [3, 1];
        else if (rowIndex === 3) return [5, 1];
        else if (rowIndex === 8) return [4, 1];
        else return [0, 0];
      } else if (columnIndex === 3) {
        if (rowIndex === 8) return [2, 1];
        else if (rowIndex === 9) return [0, 0];
      }
    },
    arraySpanMethodKU({ row, column, rowIndex, columnIndex }) {
      if (
        columnIndex === 0 ||
        columnIndex === 1 ||
        columnIndex === 2 ||
        columnIndex === 7
      ) {
        if (rowIndex === 0) return [4, 1];
        else if (rowIndex === 4) return [3, 1];
        else if (rowIndex === 7) return [2, 1];
        else if (rowIndex === 9) return [3, 1];
        else if (rowIndex === 12) return [4, 1];
        else if (rowIndex === 16) return [3, 1];
        else if (rowIndex === 19) return [1, 1];
        else if (rowIndex === 20) return [6, 1];
        else return [0, 0];
      }
    },
    arraySpanMethodKL({ row, column, rowIndex, columnIndex }) {
      if (
        columnIndex === 0 ||
        columnIndex === 1 ||
        columnIndex === 2 ||
        columnIndex === 7
      ) {
        if (rowIndex === 0) return [2, 1];
        else if (rowIndex === 2) return [3, 1];
        else if (rowIndex === 5) return [2, 1];
        else if (rowIndex === 7) return [4, 1];
        else return [0, 0];
      }
    },
    arraySpanMethodDP({ row, column, rowIndex, columnIndex }) {
      if (
        columnIndex === 0 ||
        columnIndex === 1 ||
        columnIndex === 2 ||
        columnIndex === 7
      ) {
        if (rowIndex === 0) return [1, 1];
        else if (rowIndex === 1) return [1, 1];
        else if (rowIndex === 2) return [1, 1];
        else if (rowIndex === 3) return [1, 1];
        else if (rowIndex === 4) return [1, 1];
        else if (rowIndex === 5) return [1, 1];
        else if (rowIndex === 6) return [1, 1];
        else if (rowIndex === 7) return [10, 1];
        else return [0, 0];
      }
    },
    arraySpanMethodDK({ row, column, rowIndex, columnIndex }) {
      if (
        columnIndex === 0 ||
        columnIndex === 1 ||
        columnIndex === 2 ||
        columnIndex === 7
      ) {
        if (rowIndex === 0) return [2, 1];
        else if (rowIndex === 2) return [2, 1];
        else return [0, 0];
      }
    },
    arraySpanMethodEM({ row, column, rowIndex, columnIndex }) {
      if (
        columnIndex === 0 ||
        columnIndex === 1 ||
        columnIndex === 2 ||
        columnIndex === 7
      ) {
        if (rowIndex === 0) return [6, 1];
        else if (rowIndex === 6) return [8, 1];
        else if (rowIndex === 14) return [3, 1];
        else return [0, 0];
      }
    },
    arraySpanMethodAI({ row, column, rowIndex, columnIndex }) {
      if (
        columnIndex === 0 ||
        columnIndex === 1 ||
        columnIndex === 2 ||
        columnIndex === 7
      ) {
        if (rowIndex === 0) return [3, 1];
        else if (rowIndex === 3) return [2, 1];
        else if (rowIndex === 5) return [5, 1];
        else if (rowIndex === 10) return [2, 1];
        else return [0, 0];
      } else if (columnIndex === 4) {
        if (rowIndex === 7) return [2, 1];
        else if (rowIndex === 8) return [0, 0];
      }
    },
    arraySpanMethodHS({ row, column, rowIndex, columnIndex }) {
      if (
        columnIndex === 0 ||
        columnIndex === 1 ||
        columnIndex === 2 ||
        columnIndex === 7
      ) {
        if (rowIndex === 0) return [1, 1];
        else if (rowIndex === 1) return [2, 1];
        else if (rowIndex === 3) return [2, 1];
        else if (rowIndex === 5) return [2, 1];
        else if (rowIndex === 7) return [1, 1];
        else return [0, 0];
      }
    },
    arraySpanMethodML({ row, column, rowIndex, columnIndex }) {
      if (
        columnIndex === 0 ||
        columnIndex === 1 ||
        columnIndex === 2 ||
        columnIndex === 7
      ) {
        if (rowIndex === 0) return [3, 1];
        else if (rowIndex === 3) return [1, 1];
        else if (rowIndex === 4) return [1, 1];
        else if (rowIndex === 5) return [1, 1];
        else if (rowIndex === 6) return [11, 1];
        else return [0, 0];
      }
    },
    arraySpanMethodLB({ row, column, rowIndex, columnIndex }) {
      if (
        columnIndex === 0 ||
        columnIndex === 1 ||
        columnIndex === 2 ||
        columnIndex === 7
      ) {
        if (rowIndex === 0) return [11, 1];
        else if (rowIndex === 11) return [2, 1];
        else if (rowIndex === 13) return [3, 1];
        else if (rowIndex === 16) return [4, 1];
        else if (rowIndex === 20) return [1, 1];
        else if (rowIndex === 21) return [2, 1];
        else if (rowIndex === 23) return [2, 1];
        else if (rowIndex === 25) return [2, 1];
        else if (rowIndex === 27) return [5, 1];
        else return [0, 0];
      }
    },
    arraySpanMethodSB({ row, column, rowIndex, columnIndex }) {
      if (
        columnIndex === 0 ||
        columnIndex === 1 ||
        columnIndex === 2 ||
        columnIndex === 7
      ) {
        if (rowIndex === 0) return [2, 1];
        else if (rowIndex === 2) return [2, 1];
        else if (rowIndex === 4) return [2, 1];
        else return [0, 0];
      }
    },
  },
  created() {
    this.getListPeriode();
    if (this.$store.getters.getGPData === null) {
      this.$router.push("tambah");
    } else {
      this.passData(this.$store.getters.getGPData);
    }
  },
};
</script>

